<template>
  <div class="heye-container pt-4">
    <div class="connections container-fluid">
      <div class="toolbar d-flex">
        <div class="hamburger" data-toggle="dropdown">
          <div class="line"></div>
          <div class="line --short"></div>
          <div class="line"></div>
        </div>

        <div class="dropdown-menu">
          <router-link :to="{name: 'Home'}" class="dropdown-item">Home</router-link>
          <router-link :to="{name: 'EditProfile'}" class="dropdown-item">Edit Profile</router-link>
          <a class="dropdown-item" href="#" @click="logout()">Logout</a>
        </div>
      </div>
      <SocialHourTimer :duration="10" />
      <div class="chat-button-wrapper">
        <router-link
          :to="{name: 'SearchingMatch'}"
          class="btn btn-outline-primary btn-heye pl-5 pr-5 br-20 text-black"
        >
          Say h
          <span class="font-weight-bold">eye</span>
        </router-link>
      </div>
      <div class="connections-wrapper pt-4">
        <div class="connections-title-wrapper">
          <span class="connection-title">Connections</span>
        </div>

        <div v-if="connections">
          <div class="connections-card__wrapper" v-if="connections.length">
            <ConnectionCard
              v-for="(connection, index) in connections"
              :key="index"
              :unread="connection.unread"
              :online="connection.online"
              :connection_info="connection"
              @imageClicked="handleImageClicked(connection)"
              @chatClicked="handleChatClicked(connection)"
            />
          </div>
          <div v-else>No connections found</div>
        </div>

        <div v-else class="text-center pt-4">
          <Spinner size="sm" />
        </div>
      </div>

      <FullPageModal
        :show_modal="(selectedConnectionInfo) ? true : false"
        @modalClosed="selectedConnectionInfo = null"
      >
        <template slot="modal-body">
          <UserProfileCard v-if="selectedConnectionInfo" :user_info="selectedConnectionInfo" />
        </template>
      </FullPageModal>
    </div>
  </div>
</template>

<script>
import ConnectionCard from "@/components/core/ConnectionCard.vue";
import Button from "@/components/core/Button.vue";
import LogoText from "@/components/core/LogoText.vue";
import Spinner from "@/components/core/Spinner.vue";
import SocialHourTimer from "@/components/core/SocialHourTimer.vue";
import FullPageModal from "@/components/core/FullPageModal.vue";
import UserProfileCard from "@/components/derived/UserProfileCard.vue";

export default {
  name: "Connections",

  components: {
    ConnectionCard,
    Button,
    LogoText,
    Spinner,
    SocialHourTimer,
    FullPageModal,
    UserProfileCard
  },

  data() {
    return {
      selectedConnectionInfo: null,
      connections: null
    };
  },

  created() {
    this.fetchConnections(() => {
      this.listenForSocket();
    });
  },

  destroyed() {
    // unsubscribe socket.io events
    window.heyeSocket.removeAllListeners("connection-is-online");
    window.heyeSocket.removeAllListeners("connection-is-offline");
  },

  methods: {
    listenForSocket() {
      if (!window.heyeSocket) return;

      window.heyeSocket.on("connection-is-online", email => {
        let conIndex = this.connections.findIndex(conn => conn.email == email);
        if (conIndex !== -1) this.connections[conIndex].online = true;
      });

      window.heyeSocket.on("connection-is-offline", email => {
        let conIndex = this.connections.findIndex(conn => conn.email == email);
        if (conIndex !== -1) this.connections[conIndex].online = false;
      });
    },

    fetchConnections(callback) {
      this.$store.dispatch("connections/fetchConnections", {
        callback: connections => {
          this.connections = connections.map(connection => {
            return {
              ...connection,
              unread: false
            };
          });
          callback();
        }
      });
    },

    handleChatClicked(connection) {
      this.$store.dispatch("connections/setActiveConnection", {
        connection,
        callback: () => {
          this.$router.push({ name: "TextChat" });
        }
      });
    },

    handleImageClicked(connection) {
      this.selectedConnectionInfo = {
        name: connection.name,
        city: connection.city,
        age: connection.age,
        intro: connection.bio,
        image_url: connection.image_url
      };
    },

    logout() {
      this.$store.dispatch("auth/logout", {
        callback: () => {
          this.$router.push({ name: "Home" });
        }
      });
    }
  }
};
</script>

