<template>
  <div class="modal fade" tabindex="-1" role="dialog" id="modal">
    <div class="modal-dialog modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-body">
          <div class="title d-flex mb-4">
            <h5 class="align-self-center font-weight-bold mb-0">
              <slot name="title"></slot>
            </h5>
            <button
              type="button"
              class="close ml-auto align-self-center"
              data-dismiss="modal"
              aria-label="Close"
              @click="close()"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="content">
            <slot name="modal-body"></slot>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "FullPageModal",
  components: {},
  props: {
    show_modal: Boolean
  },
  mounted() {},

  methods: {
    close() {
      $("#modal").modal("hide");
      this.$emit("modalClosed");
    }
  },
  watch: {
    show_modal(val) {
      $("#modal").modal(val ? "show" : "hide");
    }
  }
};
</script>

<style lang="scss" scoped>
.modal-body {
  font-size: 21px;
  color: $app-black;

  .content {
    color: $light-grey;
  }
}
</style>