<template>
  <div class="heye-container">
    <div class="searching-match">
      <div class="searching-match__title-wrapper">
        <h2 class="searching-match__title">You've got this.</h2>
      </div>
      <div class="searching-match__message-wrapper mt-4">
        <p class="searching-match__message">Connecting you with your next match</p>
      </div>

      <button @click="leaveMatch()">Leave</button>

      <div class="loading-match__wrapper mt-4">
        <div class="searching-dot --first"></div>
        <div class="searching-dot --second"></div>
        <div class="searching-dot --third"></div>
        <div class="searching-dot --fourth"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "SearchingMatch",

  data() {
    return {
      timer: null
    };
  },

  created() {
    // Automatically return to connections after 1 min if no-one joined
    this.timer = setTimeout(() => {
      this.leaveMatch();
      this.$router.push({ name: "VideoChat" });
    }, 60 * 1000);
    this.joinMatchPool();

    window.heyeSocket.on("lets-begin-video-chat", () => {
      console.log("lets-begin-video-chat");
      this.$router.push({ name: "VideoChat", params: { role: "receiver" } });
    });

    window.heyeSocket.on("you-are-the-initiator", () => {
      console.log("you-are-the-initiator");
      this.$router.push({ name: "VideoChat", params: { role: "initiator" } });
    });
  },

  destroyed() {
    clearTimeout(this.timer);

    // unsubscribe socket.io events
    window.heyeSocket.removeAllListeners("lets-begin-video-chat");
    window.heyeSocket.removeAllListeners("you-are-the-initiator");
  },

  methods: {
    joinMatchPool() {
      let userData = this.$store.state.auth.user;
      window.heyeSocket.emit("i-am-ready-to-match", {
        email: userData.email,
        age: userData.details.age,
        dating_preference: userData.details.dating_preference,
        gender: userData.details.gender
      });
    },

    leaveMatch() {
      let userData = this.$store.state.auth.user;
      window.heyeSocket.emit("i-want-to-leave-match-pool", userData.email);
    }
  }
};
</script>

