<template>
  <Modal :show_modal="show_modal" :show_overlay="false">
    <template slot="modal-body">
      <div class="prompt__wrapper py-3 text-center">
        <div class="prompt__question">
          <p>{{message}}</p>
        </div>
        <div class="prompt__options">
          <CheckBoxGroup
            id="next-search-prompt-questions"
            :options="[{'name':'Cancel','value':'cancel'},{'name':'Yes','value':'yes'}]"
            :inline="true"
            v-model="selectedResponse"
            @input="handleInput()"
          />
        </div>
        <div class="prompt__msg font-weight-light">
          <p>Remember to say goodbye!</p>
        </div>
      </div>
    </template>
  </Modal>
</template>

<script>
import Modal from "@/components/core/Modal.vue";
import CheckBoxGroup from "@/components/core/CheckBoxGroup.vue";

export default {
  name: "NextSearchPrompt",
  components: {
    Modal,
    CheckBoxGroup
  },
  props: { show_modal: Boolean, message: String },
  data() {
    return {
      selectedResponse: []
    };
  },
  methods: {
    handleInput() {
      this.$emit("input", this.selectedResponse);
      // console.log(this.selectedResponse);
    }
  },
  watch: {
    selectedResponse: function() {
      this.$emit("input", this.selectedResponse);
    }
  }
};
</script>

<style lang="scss" scoped>
.prompt__msg {
  color: $light-gray;
}
</style>