<template>
  <div class="text-chat d-flex flex-column" ref="chatScreen">
    <div class="text-chat__header d-flex">
      <div class="back-button justify-content-flex-end left align-self-center">
        <i class="fal fa-angle-left h-100" @click="$router.push({name: 'Connections'})"></i>
      </div>
      <div class="text-chat__user-info center">
        <img :src="activeConnection.image_url" class="text-chat__image" alt />
        <div class="text-chat__name">
          <span>{{activeConnection.name}}</span>
        </div>
      </div>
      <div class="right"></div>
    </div>

    <div class="messages d-flex flex-column-reverse" ref="messages">
      <div
        v-for="(message, index) in messages"
        :key="index"
        class="message flex-shrink-0 faster d-flex"
        :class="{'--mine justify-content-end': message.mine, '--other': !message.mine}"
      >
        <span v-if="message.type === 'text'">{{message.content}}</span>

        <span v-else-if="message.type === 'thumbs-up'" class="thumbs-up">
          <i class="fas fa-thumbs-up"></i>
        </span>

        <span v-else-if="message.type === 'heart'" class="thumbs-up">
          <i class="fas fa-heart"></i>
        </span>
      </div>

      <!-- Fade Up animation -->
      <div v-if="messages.length < 10" class="fade-up-animation-helper"></div>
    </div>

    <div class="chat-controls">
      <div class="d-flex align-items-center">
        <input
          type="text"
          class="message-input flex-grow-1"
          v-model="messageInput"
          @keyup.enter="sendMessage('text')"
        />

        <i class="icon fal fa-arrow-circle-up align-self-center ml-1" @click="sendMessage('text')"></i>

        <!-- <div class="send-chat-button">
            <i class="fas fa-arrow-alt-up"></i>
        </div>-->
      </div>
    </div>
  </div>
</template>

<script>
import { random } from "@/helpers/core";
import BackMenu from "@/components/core/BackMenu.vue";

export default {
  name: "TextChat",
  components: { BackMenu },
  data() {
    return {
      showPrompt: true,
      selectedResponse: [],
      messageInput: null,
      messages: []
    };
  },

  created() {
    setTimeout(() => {
      if (!this.activeConnection) this.$router.push({ name: "Connections" });
    }, 1000);
    this.listenForSocket();
    this.fetchMessages();
  },

  methods: {
    fetchMessages() {
      this.$store.dispatch("messages/fetchMessages", {
        roomname: this.activeConnection.roomname,
        callback: messages => {
          this.messages = messages
            .map(m => {
              return {
                type: "text",
                content: m.message,
                mine: m.senderEmail == this.activeConnection.email
              };
            })
            .reverse();
        }
      });
    },

    listenForSocket() {
      // Join chat
      window.heyeSocket.emit("tchat-join", this.activeConnection.roomname);

      window.heyeSocket.on("tchat-message", message => {
        this.messages.unshift({
          type: "text",
          content: message,
          mine: false
        });
      });
    },

    sendMessage(type) {
      if (this.messageInput && this.messageInput.length) {
        this.messages.unshift({ type, content: this.messageInput, mine: true });
        window.heyeSocket.emit("tchat-message", {
          senderEmail: this.activeConnection.email,
          roomname: this.activeConnection.roomname,
          message: this.messageInput
        });
        this.messageInput = null;
      }
      this.$refs.messages.scrollTop = this.$refs.messages.scrollHeight;
    }
  },

  computed: {
    activeConnection() {
      return this.$store.state.connections.activeConnection;
    }
  }
};
</script>

