import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home'
import Login from '../views/Login'
import Register from '../views/Register'
import ProcessRegistration from '../views/ProcessRegistration'
import VideoChat from '../views/VideoChat'
import TextChat from '../views/TextChat'
import Connections from '../views/Connections'
import Ready from '../views/ReadyForSearch'
import EditProfile from '../views/EditProfile.vue'
import SearchingForMatch from '../views/SearchingMatch.vue'
import store from '../store'

Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'Home',
        component: Home
    },

    {
        path: '/login',
        name: 'Login',
        component: Login
    },

    {
        path: '/register',
        name: 'Register',
        component: Register
    },

    {
        path: '/process-register',
        name: 'ProcessRegistration',
        component: ProcessRegistration
    },

    {
        path: '/video-chat',
        name: 'VideoChat',
        component: VideoChat
    },

    {
        path: '/text-chat',
        name: 'TextChat',
        component: TextChat
    },

    {
        path: '/connections',
        name: 'Connections',
        component: Connections
    },

    {
        path: '/ready',
        name: 'Ready',
        component: Ready
    },

    {
        path: '/dashboard',
        name: 'Dashboard',
        component: () =>
            import ('../views/Dashboard.vue')
    },

    {
        path: '/edit-profile',
        name: 'EditProfile',
        component: EditProfile
    },

    {
        path: '/searching',
        name: 'SearchingMatch',
        component: SearchingForMatch
    }
]

const router = new VueRouter({
    routes
})

