import { http } from "@/helpers/http";
import Vue from 'vue'

export default {
    namespaced: true,
    state: {
        ads: null
    },

    getters: {
        ad(state) {
            return state.ads;
        }
    },

    mutations: {
        setAds(state, ads) {
            Vue.set(state, 'ads', ads);
        }
    },
    actions: {
        sendOffer({ state, commit, rootState }, payload) {
            http.post("/rtc/send-offer", payload.offer).then(response => {
                if (payload.callback) payload.callback(true);
            })
        },

        sendAnswer({ state, commit, rootState }, payload) {
            http.post("/rtc/send-answer", payload.answer).then(response => {
                if (payload.callback) payload.callback(true);
            })
        },

        requestOffer({ state, commit, rootState }, payload) {
            http.get("/rtc/request-offer").then(response => {
                if (payload.callback) payload.callback(true, response.data);
            }).catch(error => {
                if (payload.callback) payload.callback(false);
            })
        },

        requestAnswer({ state, commit, rootState }, payload) {
            http.get("/rtc/request-answer").then(response => {
                if (payload.callback) payload.callback(true, response.data);
            }).catch(error => {
                if (payload.callback) payload.callback(false);
            })
        }
    }
}