<template>
  <div class="ready-wapper">
    <SocialHourTimer :duration="2400" class="ready-social-timer" />
    <div class="ready-questions-wrapper heye-container">
      <div class="ready-question__title">
        <h2>Are you Ready?</h2>
        <div class="ready-questions">
          <ReadyCheckBox
            :options="[{name:'Presentable?',value:'presentable'},{name:'Confident?',value:'confident'}]"
            v-model="readyCheckBox"
          />
        </div>
      </div>
      <transition name="fade">
        <Button v-if="showButton" variant="lightpurple" class="ready-button">
          <LogoText />
        </Button>
      </transition>
    </div>
  </div>
</template>

<script>
import SocialHourTimer from "@/components/core/SocialHourTimer.vue";
import ReadyCheckBox from "@/components/derived/ReadyCheckBox.vue";
import Button from "@/components/core/Button.vue";
import LogoText from "@/components/core/LogoText.vue";

export default {
  name: "ReadyForSearch",
  components: {
    SocialHourTimer,
    ReadyCheckBox,
    Button,
    LogoText
  },
  data() {
    return {
      showButton: false,
      readyCheckBox: []
    };
  },
  watch: {
    readyCheckBox: function() {
      if (this.readyCheckBox.length > 2) {
        this.showButton = true;
      }
    }
  }
};
</script>

