import { http } from "@/helpers/http";

export default {
    namespaced: true,
    state: {},

    getters: {},

    mutations: {},

    actions: {
        fetchMessages({ state, commit, rootState }, payload) {
            http.get("/api/messages?roomname=" + payload.roomname).then(response => {
                if (payload) payload.callback(response.data.messages);
            })
        }
    }
}