<template>
  <div class="ready-check-boxes">
    <div class="checkbox-group-wrapper">
      <template v-for="(option,i) in options">
        <label class="checkbox-container" :key="i">
          {{option.name}}
          <input
            type="checkbox"
            :value="option.value"
            :checked="option.value == value"
            v-model="checkedValues"
            @change="updateValue()"
          />
          <span class="checkmark" :class="checkmark_class"></span>
        </label>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  name: "ReadyCheckBox",
  props: {
    options: {
      type: Array,
      required: true
    },

    value: {
      type: null
    }
  },

  data: function() {
    return {
      checkedValues: []
    };
  },

  created() {
    if (this.value) {
      this.checkedValues.push(this.value);
    }
  },

  methods: {
    updateValue() {
      let valueToReturn = this.checkedValues;
      this.$emit("input", valueToReturn);
    }
  }
};
</script>

<style lang="scss" scoped>
.checkbox-container {
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 24px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: flex;
  align-items: center;
  border-bottom: 1px solid $light-purple;
}

.ready-check-boxes{
    padding: 20px;
}

.checkbox-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  position: absolute;
  top: 9px;
  left: 0;
  height: 20px;
  border: 1px solid $white;
  border-radius: 50%;
  width: 20px;
  background-color: $purple;
}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.checkbox-container input:checked ~ .checkmark:after {
  display: block;
}

.checkbox-container .checkmark:after {
  left: 6px;
  top: 3px;
  width: 5px;
  height: 10px;
  border: solid $black;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.checkbox-container input:checked ~ .checkmark {
  background-color: $white;
}
</style>
