<template>
  <div class="button-wrapper">
    <button :class="buttonClass" @click="$emit('click')">
      <slot></slot>
    </button>
  </div>
</template>

<script>
export default {
  name: "Button",
  props: {
    custom_class: String,
    variant: String
  },
  computed: {
    buttonClass: function() {
      if (!this.custom_class) {
        return "btn heye-button --" + this.variant;
      }
      return "btn heye-button --" + this.variant + " " + this.custom_class;
    }
  }
};
</script>

<style lang="scss" scoped>
.heye-button {
  border-radius: 20px;
  padding: 5px 60px 5px 60px;
  font-size: 20px;
  &.--primary {
    background-color: $purple;
    color: $white;
  }
  &.--secondary {
    border-color: $purple;
    color: $purple;
  }
  &.--lightpurple{
    border-color: transparent;
    background-color: $light-purple;
  }
}
</style>