import { http } from "@/helpers/http";
import Vue from 'vue'

export default {
    namespaced: true,
    state: {
        token: null,
        user: null,
        onlineTimer: null
    },

    getters: {
        token(state) {
            return state.token;
        },

        user(state) {
            return state.user;
        }
    },

    mutations: {
        setToken(state, token) {
            Vue.set(state, 'token', token);
        },

        setUser(state, user) {
            Vue.set(state, 'user', user);
        }
    },

    actions: {
        login({ state, commit, rootState }, payload) {
            http.post("/auth/login", payload.data)
                .then(response => {
                    commit('setToken', response.data.token);

                    // axios
                    http.defaults.headers.common['authorization'] = 'Bearer ' + response.data.token;

                    // local storage
                    localStorage.setItem("_token", response.data.token);

                    // init socket
                    window.heyeSocket = io("http://192.168.100.3:8002/heye");

                    // make sure the user remains online
                    if (!state.onlineTimer)
                        state.onlineTimer = setInterval(() => {
                            heyeSocket.emit('i-am-online', payload.data.email);
                        }, 3000);

                    if (payload.callback) payload.callback(true);
                })
                .catch(error => {
                    if (payload.callback) payload.callback(false, error.response);
                })
        },

        logout({ state, commit, rootState }, payload) {
            commit('setToken', null);

            // axios
            http.defaults.headers.common['authorization'] = null;

            // local storage
            localStorage.removeItem("_token");

            // disconnect socket
            if (window.heyeSocket) window.heyeSocket.disconnect();

            if (payload.callback) payload.callback();
        },

        loadToken({ state, commit, rootState }, payload) {
            let token = localStorage.getItem('_token');
            if (token) {
                commit('setToken', token);
                http.defaults.headers.common['authorization'] = 'Bearer ' + token;

                // init socket
                if (!window.heyeSocket) {
                    // get email
                    http.get("/api/user/me").then(response => {
                        console.log(response.data);
                        window.heyeSocket = io("http://192.168.100.3:8002/heye");
                        heyeSocket.emit('i-am-online', response.data.email);
                        commit("setUser", response.data);
                        payload.callback();
                    });
                    payload.callback();
                    return;
                }
                payload.callback();
            }
            payload.callback();
        }
    }
}