import { http } from "@/helpers/http";
import Vue from 'vue'

export default {
    namespaced: true,
    state: {},

    getters: {},

    mutations: {},

    actions: {
        updateProfile({ state, commit, rootState }, payload) {
            http.post("/api/user/update", payload.data)
                .then(response => {
                    if (payload.callback) payload.callback(true);
                })
                .catch(error => {
                    if (payload.callback) payload.callback(false, error.response);
                })
        },
    }
}