<template>
  <div class="register">
    <div class="heye-container container-fluid">
      <div class="container text-center mt-5">
        <p class="lead font-weight-bold">Edit Profile Information</p>

        <form @submit.prevent="handleRegister" v-if="editProfileForm">
          <!-- Profile pic -->
          <div class="row">
            <div class="col p-1">
              <picture-input
                ref="pictureInput"
                :prefill="editProfileForm.profile_preview"
                width="100"
                height="100"
                radius="50"
                margin="16"
                accept="image/jpeg, image/png"
                size="10"
                button-class="btn btn-sm btn-primary"
                :custom-strings="{drag: 'Profile Picture', upload: '<h1>Bummer!</h1>'}"
                @change="onChange"
              ></picture-input>
            </div>
          </div>

          <!-- First & last name -->
          <div class="row">
            <div class="col p-1">
              <div class="form-group">
                <input
                  type="text"
                  class="form-control"
                  placeholder="First Name"
                  v-model="editProfileForm.first_name"
                />
              </div>
            </div>
            <div class="col p-1">
              <div class="form-group">
                <input
                  type="text"
                  class="form-control"
                  placeholder="Last Name"
                  v-model="editProfileForm.last_name"
                />
              </div>
            </div>
          </div>

          <!-- Email & Phone -->
          <div class="row">
            <div class="col p-1">
              <div class="form-group">
                <input
                  type="email"
                  class="form-control"
                  placeholder="Email"
                  disabled
                  v-model="editProfileForm.email"
                />
              </div>
            </div>
            <div class="col p-1">
              <div class="form-group">
                <input
                  type="text"
                  class="form-control"
                  placeholder="Phone number"
                  v-model="editProfileForm.phone_number"
                />
              </div>
            </div>
          </div>

          <!-- Age & Address -->
          <div class="row">
            <div class="col p-1">
              <div class="d-flex flex-column">
                <label class="mr-2 text-left">Birthday</label>
                <select class="form-control mb-1" v-model="editProfileForm.age.day">
                  <option v-for="d in 30" :key="d" :value="d">{{d}}</option>
                </select>

                <select class="form-control mb-1" v-model="editProfileForm.age.month">
                  <option v-for="month in months" :key="month" :value="month">{{month}}</option>
                </select>

                <select class="form-control mb-1" v-model="editProfileForm.age.year">
                  <option v-for="y in yearRange" :key="y" :value="y">{{y}}</option>
                </select>
              </div>
            </div>

            <div class="col p-1">
              <div class="d-flex flex-column">
                <label class="mr-2 text-left">Location</label>
                <select
                  class="form-control align-self-center mb-1"
                  v-model="editProfileForm.location.state"
                  @change="handleStateSelected()"
                >
                  <option
                    v-for="(state, index) in states"
                    :key="index"
                    :value="state"
                    :selected="state == editProfileForm.location.state"
                  >{{state}}</option>
                </select>
                <select
                  class="form-control align-self-center"
                  v-model="editProfileForm.location.city"
                >
                  <option
                    v-for="(city, index) in cities"
                    :key="index"
                    :value="city"
                    :selected="city == editProfileForm.location.city"
                  >{{city}}</option>
                </select>
              </div>
            </div>
          </div>

          <!-- Bio -->
          <div class="row">
            <div class="col p-1">
              <div class="form-group text-left">
                <textarea
                  class="form-control"
                  rows="3"
                  placeholder="(Line Bio) tell us about yourself"
                  v-model="editProfileForm.bio"
                  maxlength="50"
                ></textarea>
                <label class="mr-auto font-weight-bold text-secondary">
                  <small>Count: {{editProfileForm.bio ? editProfileForm.bio.length : 0}}</small>
                </label>
              </div>
            </div>
          </div>

          <!-- Gender & Dating preference -->
          <div class="row">
            <div class="col p-1 text-left">
              <label>Gender</label>
              <br />
              <div class="form-check">
                <input
                  class="form-check-input"
                  type="radio"
                  name="gender"
                  id="gender-male"
                  value="male"
                  v-model="editProfileForm.gender"
                />
                <label class="form-check-label" for="gender-male">Male</label>
              </div>
              <div class="form-check">
                <input
                  class="form-check-input"
                  type="radio"
                  name="gender"
                  id="gender-female"
                  value="female"
                  v-model="editProfileForm.gender"
                />
                <label class="form-check-label" for="gender-female">Female</label>
              </div>
              <div class="form-check">
                <input
                  class="form-check-input"
                  type="radio"
                  name="gender"
                  id="gender-other"
                  value="neither"
                  v-model="editProfileForm.gender"
                />
                <label class="form-check-label" for="gender-other">Neither</label>
              </div>
            </div>
            <div class="col p-1 text-left">
              <label>Dating Preference</label>
              <br />
              <div class="form-check">
                <input
                  class="form-check-input"
                  type="radio"
                  name="dating-preference"
                  id="gender-male-alt"
                  value="male"
                  v-model="editProfileForm.dating_preference"
                />
                <label class="form-check-label" for="gender-male-alt">Male</label>
              </div>
              <div class="form-check">
                <input
                  class="form-check-input"
                  type="radio"
                  name="dating-preference"
                  id="gender-female-alt"
                  value="female"
                  v-model="editProfileForm.dating_preference"
                />
                <label class="form-check-label" for="gender-female-alt">Female</label>
              </div>
              <div class="form-check">
                <input
                  class="form-check-input"
                  type="radio"
                  name="dating-preference"
                  id="gender-other-alt"
                  value="non-bianary"
                  v-model="editProfileForm.dating_preference"
                />
                <label class="form-check-label" for="gender-other-alt">Non-Bianary</label>
              </div>
            </div>
          </div>

          <!-- Insta handle -->
          <div class="row">
            <div class="col p-1">
              <div class="form-group mb-2">
                <input
                  type="text"
                  class="form-control"
                  placeholder="Instagram Handle"
                  v-model="editProfileForm.insta_handle"
                />
              </div>
            </div>
          </div>

          <button
            type="submit"
            class="btn btn-outline-primary pl-4 pr-4 br-20 mb-4 btn-heye"
          >Update Info</button>
        </form>

        <div v-else>Loading...</div>
      </div>
    </div>
  </div>
</template>

<script>
import PictureInput from "vue-picture-input";
import { ADDRESS } from "@/data/address";
import { MONTHS } from "@/data/date";
import { http } from "@/helpers/http";

export default {
  name: "Register",
  components: { PictureInput },
  data() {
    return {
      editProfileForm: null,
      states: null,
      cities: null,
      months: MONTHS
    };
  },

  created() {
    this.states = Object.keys(ADDRESS);
    this.fetchProfileDetails();
  },

  methods: {
    fetchProfileDetails() {
      http.get("/api/user/me").then(response => {
        let data = response.data;
        this.editProfileForm = {
          first_name: data.details.first_name,
          last_name: data.details.last_name,
          phone_number: data.details.phone_number,
          age: data.details.age,
          location: data.details.location,
          gender: data.details.gender,
          dating_preference: data.details.dating_preference,
          profile_preview: data.details.profile_pic,
          profile_pic: null,
          insta_handle: data.details.insta_handle
        };
        this.handleStateSelected();
      });
    },

    handleRegister() {
      this.$store.dispatch("user/updateProfile", {
        data: this.editProfileForm,
        callback: () => {
          this.$router.push({ name: "Connections" });
        }
      });
    },

    handleStateSelected() {
      this.cities = ADDRESS[this.editProfileForm.location.state];
    },

    onChange(image) {
      if (image) {
        this.editProfileForm.profile_pic = image;
      } else {
        console.log("FileReader API not supported: use the <form>, Luke!");
      }
    }
  },

  computed: {
    yearRange() {
      let currentYear = new Date().getFullYear();
      let yearRange = [];
      for (let i = 1940; i <= currentYear; i++) yearRange.push(i);
      return yearRange;
    }
  }
};
</script>

<style lang="scss" scoped>
.form-group {
  margin-bottom: 0.1rem;
}

::v-deep .picture-inner {
  top: -105px !important;
}

::v-deep .picture-preview {
  border-radius: 50% !important;
}
</style>