<template>
  <div class="login">
    <div class="heye-container container-fluid pt-5">
      <div class="container text-center mt-5">
        <h1 class="login-title-text text-capitalize">Are you ready ?</h1>

        <div class="mb-5">
          <ul class="login-rules">
            <li>
              <div class="d-flex">
                <div class="circle-bullet align-self-center"></div>
                <span class="align-self-center">Be Presentable</span>
              </div>
            </li>
            <li>
              <div class="d-flex">
                <div class="circle-bullet align-self-center"></div>
                <span class="align-self-center">Be Confident</span>
              </div>
            </li>
          </ul>
        </div>

        <input
          type="text"
          class="form-control mb-3 br-20"
          v-model="email"
          placeholder="Email address"
          :disabled="loading"
        />
        <input
          type="password"
          class="form-control mb-3 br-20"
          v-model="password"
          placeholder="Password"
          :disabled="loading"
        />

        <button
          class="login__button btn btn-primary br-20 pl-4 pr-4 mt-2 mb-4"
          @click="login()"
        >LOGIN</button>

        <p>
          <router-link :to="{name: 'Register'}" class="--no-color">Submit application</router-link>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  components: {},
  data() {
    return {
      email: "test1@heye.com",
      password: "password",
      loading: false
    };
  },

  methods: {
    login() {
      this.loading = true;
      this.$store.dispatch("auth/login", {
        data: {
          email: this.email,
          password: this.password
        },
        callback: (status, data) => {
          if (status) this.$router.push({ name: "Connections" });
          else this.$toasted.show(data.data.message);
          this.loading = false;
        }
      });
    }
  }
};
</script>

