<template>
  <div :id="id" :class="custom_class">
    <div class="checkbox-group-wrapper" v-bind:class="{'has-inline':inline}">
      <template v-for="(option,i) in options">
        <label class="checkbox-container" :key="i">
          {{option.name}}
          <input
            type="checkbox"
            :value="option.value"
            :checked="option.value == value"
            v-model="checkedValues"
            @change="onChange()"
          />
          <span class="checkmark" :class="checkmark_class"></span>
        </label>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  name: "CheckBoxGroup",
  props: {
    options: {
      type: Array,
      required: true
    },

    custom_class: {
        type: String
    },

    id: {
      type: String,
      required: true
    },

    value: {
      type: null
    },

    checkmark_class: {
        type: String
    },
    inline: false

  },

  data: function() {
    return {
      checkedValues: []
    };
  },

  methods: {
    onChange() {
      let valueToReturn =
        this.checkedValues.length === 0
          ? this.checkedValues[0]
          : this.checkedValues;
      this.$emit("input", valueToReturn);
    }
  }
};
</script>

<style lang="scss" scoped>
/* The container */
.checkbox-container {
  position: relative;
  padding-left: 28px;
  margin-bottom: 12px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: flex;
  align-items: center;
}

/* Hide the browser's default checkbox */
.checkbox-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: 3px;
  left: 0;
  height: 20px;
  border-radius: 50%;
  border: 1px solid $black;
  width: 20px;
  background-color: #eee;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.checkbox-container input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.checkbox-container .checkmark:after {
  left: 6px;
  top: 3px;
  width: 5px;
  height: 10px;
  border: solid $black;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.has-inline{
  label{
    display: inline-block;
    margin-right: 15px;
  }
}
</style>
