import { http } from "@/helpers/http";
import Vue from 'vue'

export default {
    namespaced: true,
    state: {
        activeConnection: null
    },

    getters: {
        activeConnection(state) {
            return state.activeConnection;
        }
    },

    mutations: {
        setActiveConnection(state, connection) {
            state.activeConnection = connection;
        }
    },

    actions: {
        fetchConnections({ state, commit, rootState }, payload) {
            http.get("/api/connections").then(response => {
                if (payload) payload.callback(response.data.connections);
            })
        },

        setActiveConnection({ state, commit, rootState }, payload) {
            commit('setActiveConnection', payload.connection);
            if (payload.callback) payload.callback();
        }
    }
}