<template>
  <div class="register">
    <div class="heye-container container-fluid">
      <div class="container text-center mt-5">
        <p class="lead font-weight-bold">Application for heye</p>

        <form @submit.prevent="handleRegister">
          <!-- Profile pic -->
          <div class="row">
            <div class="col p-1">
              <picture-input
                ref="pictureInput"
                :crop="true"
                width="100"
                height="100"
                radius="50"
                margin="16"
                accept="image/jpeg, image/png"
                size="10"
                button-class="btn btn-sm btn-primary"
                :custom-strings="{drag: 'Tap here to select photo', select: 'Tap here to select photo'}"
                @change="onProfileChange"
              ></picture-input>
            </div>
          </div>

          <!-- First & last name -->
          <div class="row">
            <div class="col p-1">
              <div class="form-group">
                <input
                  type="text"
                  class="form-control"
                  placeholder="First Name"
                  v-model="registrationForm.first_name"
                />
              </div>
            </div>
            <div class="col p-1">
              <div class="form-group">
                <input
                  type="text"
                  class="form-control"
                  placeholder="Last Name"
                  v-model="registrationForm.last_name"
                />
              </div>
            </div>
          </div>

          <!-- Email & Phone -->
          <div class="row">
            <div class="col p-1">
              <div class="form-group">
                <input
                  type="email"
                  class="form-control"
                  placeholder="Email"
                  v-model="registrationForm.email"
                />
              </div>
            </div>
            <div class="col p-1">
              <div class="form-group">
                <input
                  type="text"
                  class="form-control"
                  placeholder="Phone Number"
                  v-model="registrationForm.phone_number"
                />
              </div>
            </div>
          </div>

          <!-- Password & confirmation -->
          <div class="row">
            <div class="col p-1">
              <div class="form-group">
                <input
                  type="password"
                  class="form-control"
                  placeholder="Password"
                  v-model="registrationForm.password"
                />
              </div>
            </div>
            <div class="col p-1">
              <div class="form-group">
                <input
                  type="password"
                  class="form-control"
                  placeholder="Confirm Password"
                  v-model="registrationForm.password_confirmation"
                />
              </div>
            </div>
          </div>

          <!-- Age & location -->
          <div class="row">
            <div class="col p-1">
              <div class="d-flex flex-column">
                <label class="mr-2 text-left">Birthday</label>
                <select class="form-control mb-1" v-model="registrationForm.age.day">
                  <option v-for="d in 30" :key="d" :value="d">{{d}}</option>
                </select>

                <select class="form-control mb-1" v-model="registrationForm.age.month">
                  <option v-for="month in months" :key="month" :value="month">{{month}}</option>
                </select>

                <select class="form-control mb-1" v-model="registrationForm.age.year">
                  <option v-for="y in yearRange" :key="y" :value="y">{{y}}</option>
                </select>
              </div>
            </div>

            <div class="col p-1">
              <div class="d-flex flex-column">
                <label class="mr-2 text-left">Location</label>
                <select
                  class="form-control align-self-center mb-1"
                  v-model="registrationForm.location.state"
                  @change="handleStateSelected()"
                >
                  <option
                    v-for="(state, index) in states"
                    :key="index"
                    :value="state"
                    :selected="state === registrationForm.location.state"
                  >{{state}}</option>
                </select>
                <select
                  class="form-control align-self-center"
                  v-model="registrationForm.location.city"
                >
                  <option
                    v-for="(city, index) in cities"
                    :key="index"
                    :value="city"
                    :selected="city === registrationForm.location.city"
                  >{{city}}</option>
                </select>
              </div>
            </div>
          </div>

          <!-- Bio -->
          <div class="row">
            <div class="col p-1">
              <div class="form-group text-left">
                <textarea
                  class="form-control"
                  rows="3"
                  placeholder="(Line Bio) tell us about yourself"
                  v-model="registrationForm.bio"
                  maxlength="50"
                ></textarea>
                <label class="mr-auto font-weight-bold text-secondary">
                  <small>Count: {{registrationForm.bio ? registrationForm.bio.length : 0}}</small>
                </label>
              </div>
            </div>
          </div>

          <!-- Gender & Dating preference -->
          <div class="row">
            <div class="col p-1 text-left">
              <label>Gender</label>
              <br />
              <div class="form-check">
                <input
                  class="form-check-input"
                  type="radio"
                  name="gender"
                  v-model="registrationForm.gender"
                  id="gender-male"
                  value="male"
                />
                <label class="form-check-label" for="gender-male">Male</label>
              </div>
              <div class="form-check">
                <input
                  class="form-check-input"
                  type="radio"
                  name="gender"
                  v-model="registrationForm.gender"
                  id="gender-female"
                  value="female"
                />
                <label class="form-check-label" for="gender-female">Female</label>
              </div>
              <div class="form-check">
                <input
                  class="form-check-input"
                  type="radio"
                  name="gender"
                  v-model="registrationForm.gender"
                  id="gender-other"
                  value="neither"
                />
                <label class="form-check-label" for="gender-other">Neither</label>
              </div>
            </div>
            <div class="col p-1 text-left">
              <label>Interested In</label>
              <br />
              <div class="form-check">
                <input
                  class="form-check-input"
                  type="radio"
                  name="dating-preference"
                  id="gender-male-alt"
                  value="male"
                  v-model="registrationForm.dating_preference"
                />
                <label class="form-check-label" for="gender-male-alt">Male</label>
              </div>
              <div class="form-check">
                <input
                  class="form-check-input"
                  type="radio"
                  name="dating-preference"
                  id="gender-female-alt"
                  value="female"
                  v-model="registrationForm.dating_preference"
                />
                <label class="form-check-label" for="gender-female-alt">Female</label>
              </div>
              <div class="form-check">
                <input
                  class="form-check-input"
                  type="radio"
                  name="dating-preference"
                  id="gender-other-alt"
                  value="neither"
                  v-model="registrationForm.dating_preference"
                />
                <label class="form-check-label" for="gender-other-alt">Neither</label>
              </div>
            </div>
          </div>

          <!-- Insta handle -->
          <div class="row">
            <div class="col p-1">
              <div class="form-group mb-2">
                <input
                  ref="insta_handle"
                  type="text"
                  class="form-control"
                  placeholder="Instagram Handle"
                  v-model="registrationForm.insta_handle"
                  @focus="appendAtSign()"
                  @blur="removeAtSign()"
                />
              </div>
            </div>
          </div>

          <button type="submit" class="btn btn-outline-primary btn-heye pl-4 pr-4 br-20 mb-4 mt-4">
            Join h
            <span class="font-weight-bold">eye</span>
          </button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import PictureInput from "vue-picture-input";
import { ADDRESS } from "@/data/address";
import { MONTHS } from "@/data/date";
import { http } from "@/helpers/http";

export default {
  name: "Register",
  components: { PictureInput },
  data() {
    return {
      registrationForm: {
        profile_pic: null,
        first_name: null,
        last_name: null,
        email: null,
        phone_number: null,
        password: null,
        password_confirmation: null,
        age: {
          day: null,
          month: null,
          year: null
        },
        location: {
          state: null,
          city: null
        },
        gender: null,
        dating_preference: null,
        insta_handle: null,
        bio: null
      },
      states: null,
      cities: null,
      months: MONTHS
    };
  },

  created() {
    this.states = Object.keys(location);
  },

  methods: {
    handleRegister() {
      http
        .post("/auth/register", this.registrationForm)
        .then(response => {
          this.$store.dispatch("auth/login", {
            data: {
              email: this.registrationForm.email,
              password: this.registrationForm.password
            },
            callback: () => {
              this.$router.push({ name: "ProcessRegistration" });
            }
          });
        })
        .catch(error => {
          this.$toasted.show(error.response.data.message);
        });
    },

    handleStateSelected() {
      this.cities = location[this.registrationForm.location.state];
    },

    onProfileChange(image) {
      if (image) {
        this.registrationForm.profile_pic = image;
      } else {
        console.log("FileReader API not supported");
      }
    },

    appendAtSign() {
      this.registrationForm.insta_handle =
        "@" + (this.registrationForm.insta_handle || "");

      // Double the length because Opera is inconsistent about whether a carriage return is one character or two.
      let length = this.$refs.insta_handle.value.length * 2;
      this.$refs.insta_handle.setSelectionRange(length, length);
    },

    removeAtSign() {
      if (
        this.registrationForm.insta_handle &&
        this.registrationForm.insta_handle.length &&
        this.registrationForm.insta_handle[0] === "@"
      ) {
        this.registrationForm.insta_handle = this.registrationForm.insta_handle.substring(
          1
        );
      }
    }
  },

  computed: {
    yearRange() {
      let currentYear = new Date().getFullYear();
      let yearRange = [];
      for (let i = 1940; i <= currentYear; i++) yearRange.push(i);
      return yearRange;
    }
  }
};
</script>

<style lang="scss" scoped>
.form-group {
  margin-bottom: 0.1rem;
}

::v-deep .picture-inner {
  top: -105px !important;
}
</style>