<template>
  <div class="timer-wrapper" :class="custom_class">
    <div class="timer-title">
      <span>Social Hour</span>
    </div>
    <div class="time-wrapper">
      <div class="mins-wrapper">
        <div class="mins">
          <span>{{mins}}</span>
        </div>
        <div class="mins__indicator">
          <span>mins</span>
        </div>
      </div>
      <div class="secs-wrapper">
        <div class="secs">
          <span>{{secs}}</span>
        </div>
        <div class="secs__indicator">
          <span>secs</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// Duration: seconds
export default {
  name: "SocialHourTimer",
  props: {
    duration: {
      type: Number,
      default: 100
    },
    custom_class: String
  },
  methods: {
    runTimer() {
      let self = this;
      let timer = this.duration;
      let minutes, seconds;

      setInterval(function() {
        minutes = parseInt(timer / 60, 10);
        seconds = parseInt(timer % 60, 10);

        minutes = minutes < 10 ? "0" + minutes : minutes;
        seconds = seconds < 10 ? "0" + seconds : seconds;

        self.mins = minutes;
        self.secs = seconds;

        if (--timer < 0) {
          timer = self.duration;
        }
      }, 1000);
    }
  },
  data() {
    return {
      mins: "00",
      secs: "00"
    };
  },
  mounted: function() {
    this.runTimer();
  }
};
</script>

