<template>
  <div class="connection" v-bind:class="{'--unread': unread }">
    <div class="connection__image-wrapper" v-bind:class="{'is-online':online}">
      <img
        :src="connection_info.image_url"
        :alt="connection_info.name"
        @click="$emit('imageClicked')"
      />
      <div class="unseen-message-indicator" v-bind:class="{'d-none':!unread}"></div>
    </div>
    <div class="connection__info-wrapper" @click="$emit('chatClicked')">
      <div class="connection__name">{{name}}</div>
      <div class="connection__last-text">{{connection_info.last_message}}</div>
      <div class="connection_last-text-time">{{lastMessageTime}}</div>
    </div>
  </div>
</template>

<script>
import moment from "moment";

export default {
  name: "ConnectionCard",
  props: {
    connection_info: Object,
    unread: Boolean,
    online: Boolean
  },
  methods: {
    gotoChat() {
      this.$router.push({ name: "TextChat" });
    },

    gotoProfile() {
      this.$router.push({ name: "UserProfile" });
    }
  },

  computed: {
    name() {
      return this.connection_info.name.length > 10
        ? this.connection_info.name.slice(0, 10) + "..."
        : this.connection_info.name;
    },

    lastMessageTime() {
      if (this.connection_info.last_message_time) {
        return moment(this.connection_info.last_message_time, "x").format('hh:mm');
      }
    }
  }
};
</script>

