<template>
  <div class="video-chat" ref="chatScreen">
    <div class="top-controls">
      <div class="d-flex">
        <button @click="exit()">Exit</button>
        <button @click="next()">Next</button>
      </div>
      <p class="user-info p-2">
        John Doe
        <br />23
        <br />Texas, Plano
      </p>
    </div>

    <video class="match-video" ref="matchVideo"></video>
    <video class="my-video" ref="myVideo"></video>
    <div class="messages d-flex flex-column-reverse" ref="messages">
      <!-- <div class=""> -->
      <div
        v-for="(message, index) in messages"
        :key="index"
        class="message flex-shrink-0"
        :class="{'--mine': message.mine, '--other': !message.mine}"
      >
        <span v-if="message.type === 'text'">{{message.content}}</span>

        <span v-else-if="message.type === 'thumbs-up'" class="thumbs-up">
          <i class="fas fa-thumbs-up"></i>
        </span>

        <span v-else-if="message.type === 'heart'" class="thumbs-up">
          <i class="fas fa-heart"></i>
        </span>
      </div>

      <!-- Fade Up animation -->
      <!-- <div v-if="messages.length < 10" class="fade-up-animation-helper"></div> -->
      <!-- </div> -->
    </div>

    <div class="chat-controls">
      <div class="d-flex">
        <input
          ref="messageInput"
          type="text"
          class="message-input flex-grow-1"
          v-model="messageInput"
          @keyup.enter="sendMessage('text')"
        />
        <!-- <i class="icon fal fa-heart align-self-center" @click="sendMessage('heart')"></i> -->
        <i class="icon fal fa-arrow-circle-up align-self-center ml-1" @click="sendMessage('text')"></i>
      </div>
    </div>

    <NextSearchPrompt
      :show_modal="exitMessage"
      v-model="exitSelectedResponse"
      message="Are you sure you'd like to exit the conversation?"
      @input="handleExitPromptInput"
    />

    <NextSearchPrompt
      :show_modal="nextMessage"
      v-model="nextSelectedResponse"
      message="Are you sure you'd like to move to the next conversation?"
      @input="handleNextPromptInput"
    />
  </div>
</template>

<script>
import { random } from "@/helpers/core";
import NextSearchPrompt from "@/components/derived/NextSearchPrompt.vue";
import { PerfectScrollbar } from "vue2-perfect-scrollbar";

export default {
  name: "VideoChat",
  components: { NextSearchPrompt, PerfectScrollbar },
  data() {
    return {
      connectionTimer: null,
      exitMessage: false,
      nextMessage: false,
      exitSelectedResponse: [],
      nextSelectedResponse: [],
      messageInput: null,
      messages: [
        { type: "text", content: "Hello, how are you", mine: true },
        { type: "text", content: "I'm fine. How are you ?", mine: false }
      ],

      socket: null,
      webrtc: {
        peer: null,
        offerSent: false,
        answerSent: false,
        answerReceived: false,
        offerReceived: false
      }
    };
  },

  created() {
    // if (!this.$route.params.role) this.$router.push({ name: "Connections" });
  },

  destroyed() {
    clearTimeout(this.connectionTimer);

    // unsubscribe socket.io events
    window.heyeSocket.removeAllListeners("partner-has-left");
    window.heyeSocket.removeAllListeners("v-message");
    window.heyeSocket.removeAllListeners("offer");
    window.heyeSocket.removeAllListeners("answer");
  },

  mounted() {
    //Always Keep keyboard Open
    this.$refs.messageInput.click();
    this.$refs.messageInput.focus();

    // init match-video
    // let matchVideo = this.$refs.matchVideo;
    // navigator.mediaDevices
    //   .getUserMedia({ video: true, audio: false })
    //   .then(function(stream) {
    //     matchVideo.srcObject = stream;
    //     matchVideo.play();

    //   })
    //   .catch(function(err) {
    //     console.log("An error occurred: " + err);
    //   });

    // init my-video
    // let myVideo = this.$refs.myVideo;
    // navigator.mediaDevices
    //   .getUserMedia({ video: true, audio: false })
    //   .then(stream => {
    //     myVideo.srcObject = stream;
    //     myVideo.play();
    //     this.initWebRtc(stream);
    //   })
    //   .catch(function(err) {
    //     console.log("An error occurred: " + err);
    //   });

    // this.initWebRtc();
    this.initSocket();
  },

  methods: {
    initSocket() {
      window.heyeSocket.on("v-message", message => {
        this.pushMessage(message);
      });

      window.heyeSocket.on("partner-has-left", () => {
        alert("Oops ! The other user has left");
        window.heyeSocket.emit(
          "i-want-to-leave-match-pool",
          this.$store.state.auth.user.email
        );
        this.$router.push({ name: "Connections" });
      });

      if (this.$route.params.role === "initiator") {
        // connect if they are chatting for 3 mins
        this.connectionTimer = setTimeout(() => {
          window.heyeSocket.emit("lets-connect");
        }, 1 * 60 * 1000);
      }
    },

    initWebRtc(stream) {
      console.log("---------Init Webrtc---------");
      let isInitiator = location.hash.split("#").pop() === "1";
      // debugger;

      if (isInitiator) {
        this.webrtc.peer = new SimplePeer({
          initiator: true,
          trickle: false,
          stream: stream,
          offerConstraints: {
            offerToReceiveAudio: false,
            offerToReceiveVideo: true
          }
        });
      } else {
        this.webrtc.peer = new SimplePeer({
          initiator: false,
          trickle: false,
          stream: stream,
          answerConstraints: {
            offerToReceiveAudio: false,
            offerToReceiveVideo: true
          }
        });
      }

      this.webrtc.peer.on("error", err => console.log("error", err));

      this.webrtc.peer.on("signal", data => {
        if (isInitiator) {
          window.heyeSocket.emit("offer", data);
          console.log("Offer sent");
        } else {
          window.heyeSocket.emit("answer", data);
          console.log("Answer sent");
        }
      });

      window.heyeSocket.on("offer", offer => {
        alert("Offer received");
        this.webrtc.peer.signal(offer);
      });
      window.heyeSocket.on("answer", answer => {
        alert("Answer received");
        console.log(answer);
        this.webrtc.peer.signal(answer);
      });

      this.webrtc.peer.on("connect", () => {
        console.log("----WEBRTC CONNECTED-----");
        // this.webrtc.peer.send("whatever" + Math.random());
      });

      // this.webrtc.peer.on("data", data => {
      //   console.log("data: " + data);
      // });

      let matchVideo = this.$refs.matchVideo;
      this.webrtc.peer.on("stream", otherStream => {
        matchVideo.srcObject = otherStream;
        matchVideo.play();
        console.log(otherStream);
        console.log("@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@");
      });
    },

    handleExitPromptInput() {
      if (this.exitSelectedResponse && this.exitSelectedResponse.length > 0) {
        if (this.exitSelectedResponse[0] === "yes") {
          window.heyeSocket.emit(
            "i-want-to-leave-match-pool",
            this.$store.state.auth.user.email
          );
          this.$router.push({ name: "Connections" });
        }
        this.exitMessage = false;
      }
    },

    handleNextPromptInput() {
      if (this.nextSelectedResponse && this.nextSelectedResponse.length > 0) {
        if (this.nextSelectedResponse[0] === "yes") {
          window.heyeSocket.emit(
            "i-want-to-leave-match-pool",
            this.$store.state.auth.user.email
          );
          this.$router.push({ name: "SearchingMatch" });
        }
        this.nextMessage = false;
      }
    },

    stopStreamedVideo(videoElem) {
      debugger;
      const stream = videoElem.srcObject;
      const tracks = stream.getTracks();
      tracks.forEach(function(track) {
        track.stop();
      });
      videoElem.srcObject = null;
    },

    sendMessage(type) {
      if (this.messageInput && this.messageInput.length) {
        this.messages.unshift({ type, content: this.messageInput, mine: true });
        window.heyeSocket.emit("v-message", this.messageInput);
        this.messageInput = null;
      }
      this.$refs.messages.scrollTop = this.$refs.messages.scrollHeight;
    },

    pushMessage(message) {
      this.messages.unshift({
        type: "text",
        content: message,
        mine: false
      });
    },

    exit() {
      this.exitMessage = true;
    },

    next() {
      this.nextMessage = true;
    }
  }

  // watch: {
  //   $route: (to, from) => {
  //     this.stopStreamedVideo(this.$refs.myVideo);
  //     this.stopStreamedVideo(this.$refs.matchVideo);
  //   }
  // }
};
</script>

